<template>
  <div>
    <div class="text-center">欢迎, </div>
    <div id="username_display" class="display-6">{{ this.username }}</div>
    <button id="sign_out" class="mt-4 btn btn-danger" @click="signOut">
      登出
    </button>
  </div>
</template>

<script>

export default {
  data() {
    return {
      username: localStorage.getItem("username"),
    };
  },
  methods: {
    signOut() {
      this.$message({
        showClose: true,
        message: '登出成功',
        type: 'success'
      });
      this.$router.push("/");
    },
  },
};
</script>
