<template>
  <div class="container">
    <form @submit.prevent="register">
      <h2 class="mb-3">注册</h2>
      <div class="input">
        <label for="username">用户名</label>
        <input
          class="form-control"
          type="text"
          name="username"
          placeholder="左日月木辛"
        />
      </div>
      <div class="input">
        <label for="password">密码</label>
        <input
          class="form-control"
          type="password"
          name="password"
          placeholder="密码"
        />
      </div>

      <div class="alternative-option mt-4">
        已有账号? <span @click="moveToLogin">前往登录</span>
      </div>

      <button type="submit" id="register_button" class="mt-4 btn-pers">
        注册
      </button>
      <div
        class="alert alert-warning alert-dismissible fade show mt-5 d-none"
        role="alert"
        id="alert_2"
      >
        Lorem ipsum dolor sit amet consectetur, adipisicing elit.
        <button
          type="button"
          class="btn-close"
          data-bs-dismiss="alert"
          aria-label="Close"
        ></button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    register(submitEvent) {
      this.username = submitEvent.target.elements.username.value;
      this.password = submitEvent.target.elements.password.value;
      axios.post('https://main.xyldomain.top:23808/api/register', {"username": this.username, "password": this.password})
        .then((res) => {
          console.log(res.data)
          if (res.data.data) {
            this.$message({
              showClose: true,
              message: '注册成功',
              type: 'success'
            });
            this.$router.push("/");
          } else {
            this.$message({
              showClose: true,
              message: res.data.errMsg,
              type: 'error'
            });
          }
        });
    },
    moveToLogin() {
      this.$router.push("/");
    },
  },
};
</script>
